<template>
  <ValidationProvider v-slot="{ errors }" :rules="validationRules">
    <TimeField
      :value="_value"
      :is-readonly="isReadonly"
      :is-disabled="isDisabled"
      :placeholder="placeholder"
      :error="errors[0]"
      @input="handleInput"
    />
  </ValidationProvider>
</template>

<script>
import { ValidationProvider } from "vee-validate";
import TimeField from "./TimeField.vue";

export default {
  name: "TimeFieldWrapper",

  components: { ValidationProvider, TimeField },

  props: {
    value: {
      type: String,
      required: true,
    },

    field: {
      type: Object,
      required: true,
    },
  },

  computed: {
    validationRules() {
      const rules = {};

      if (this.isMandatory && !this.isReadonly) {
        rules.required = true;
      }

      const minTime = this.field.settings.validation.minimum;
      if (minTime) {
        rules.minTime = minTime;
      }

      const maxTime = this.field.settings.validation.maximum;
      if (maxTime) {
        rules.maxTime = maxTime;
      }

      return rules;
    },

    isReadonly() {
      return this.field.settings.general.visibility === "READ_ONLY";
    },

    isDisabled() {
      return this.field.settings.general.visibility === "DISABLE";
    },

    isMandatory() {
      return this.field.settings.validation.fieldRule === "REQUIRED";
    },

    placeholder() {
      return this.field.settings.general.placeholder;
    },

    _value() {
      if (this.value) {
        return this.value;
      }

      if (this.field.settings.specific.defaultValue === "CURRENT_TIME") {
        return this.$day.newTime();
      }

      return this.field.settings.specific.customDefaultValue;
    },
  },

  methods: {
    handleInput(value) {
      this.$emit("input", value);
    },
  },
};
</script>

<style lang="scss" scoped></style>
