<template>
  <div id="text-area-field">
    <span
      v-if="!openLabelInput && label"
      class="label"
      @click="handleOpenInput"
    >
      <FormFieldLabel
        v-if="label"
        :label="label"
        :is-mandatory="isMandatory"
        :tooltip="tooltip"
      />
    </span>

    <input
      v-if="openLabelInput"
      ref="focusMe"
      :value="label"
      type="text"
      autofocus
      class="label1"
      :class="!labelError ? 'q-mb-sm' : ''"
      @input="handleLabelInput"
      @mouseleave="handleFocusOut(label)"
    />

    <FormFieldError v-if="labelError" :error="labelError" class="q-mb-sm" />

    <FormFieldWrapper
      :is-focused="isFocused"
      :is-clearable="false"
      :is-readonly="isReadonly"
      :is-disabled="isDisabled"
      :has-error="!!error"
    >
      <div id="tip-tap-editor">
        <!-- toolbar -->

        <Toolbar
          @set-heading="setHeading"
          @unset-heading="unsetHeading"
          @toggle-alignment="toggleAlignment"
          @toggle-bold="toggleBold"
          @toggle-italic="toggleItalic"
          @toggle-underline="toggleUnderline"
          @toggle-bullet-list="toggleBulletList"
          @toggle-number-list="toggleNumberList"
          @increase-indent="increaseIndent"
          @decrease-indent="decreaseIndent"
          @set-text-color="setTextColor"
          @unset-text-color="unsetTextColor"
          @set-bg-color="setBgColor"
          @unset-bg-color="unsetBgColor"
          @undo="undo"
          @redo="redo"
          @set-link="setLink"
        />

        <!-- ... -->

        <!-- content -->
        <BaseScrollbar height="200px">
          <EditorContent :editor="editor" />
        </BaseScrollbar>

        <!-- ... -->
      </div>
    </FormFieldWrapper>

    <FormFieldError v-if="error" :error="error" />
  </div>
</template>

<script>
import Toolbar from "./components/toolbar/Toolbar.vue";
import { Editor, EditorContent } from "@tiptap/vue-2";
import StarterKit from "@tiptap/starter-kit";
import Link from "@tiptap/extension-link";
import Underline from "@tiptap/extension-underline";
import TextAlign from "@tiptap/extension-text-align";
import TextStyle from "@tiptap/extension-text-style";
import { Color } from "@tiptap/extension-color";
import Highlight from "@tiptap/extension-highlight";
import Placeholder from "@tiptap/extension-placeholder";
import FieldPlaceholder from "./components/field-placeholder/extension.js";
import FormFieldLabel from "@/components/common/form/FormFieldLabel.vue";
import FormFieldWrapper from "@/components/common/form/field-wrapper/FormFieldWrapper.vue";
import FormFieldError from "@/components/common/form/FormFieldError.vue";

export default {
  name: "TextEditor",

  components: {
    Toolbar,
    EditorContent,
    FormFieldLabel,
    FormFieldWrapper,
    FormFieldError,
  },

  props: {
    value: {
      type: String,
      default: "",
    },

    label: {
      type: String,
      default: "",
    },

    placeholder: {
      type: String,
      default: "",
    },

    isMandatory: {
      type: Boolean,
      default: false,
    },

    tooltip: {
      type: String,
      default: "",
    },

    isDisabled: {
      type: Boolean,
      default: false,
    },

    isReadonly: {
      type: Boolean,
      default: false,
    },

    error: {
      type: String,
      default: "",
    },

    autoFocus: {
      type: Boolean,
      default: false,
    },

    height: {
      type: String,
      default: "",
    },

    labelEdit: {
      type: Boolean,
      default: false,
    },

    panels: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      editor: null,
      isFocused: false,
      openLabelInput: false,
      labelError: "",
    };
  },

  watch: {
    editor: {
      deep: true,
      handler() {
        this.$emit("input", this.editor.getHTML());
      },
    },
  },

  created() {
    this.initiateEditor();
  },

  beforeDestroy() {
    this.editor.destroy();
  },

  methods: {
    initiateEditor() {
      this.editor = new Editor({
        content: this.value,
        extensions: [
          StarterKit,
          Link,
          Underline,
          TextAlign.configure({
            types: ["heading", "paragraph"],
          }),
          TextStyle,
          Color,
          Highlight.configure({
            multicolor: true,
          }),
          Placeholder.configure({
            placeholder: "Start typing …",
          }),
          FieldPlaceholder,
        ],
      });
    },

    setHeading(level) {
      this.editor.chain().focus().toggleHeading({ level }).run();
    },

    unsetHeading() {
      this.editor.chain().focus().clearNodes().unsetAllMarks().run();
    },

    toggleAlignment(alignment) {
      this.editor.chain().focus().setTextAlign(alignment).run();
    },

    toggleBold() {
      this.editor.chain().focus().toggleBold().run();
    },

    toggleItalic() {
      this.editor.chain().focus().toggleItalic().run();
    },

    toggleUnderline() {
      this.editor.chain().focus().toggleUnderline().run();
    },

    toggleBulletList() {
      this.editor.chain().focus().toggleBulletList().run();
    },

    toggleNumberList() {
      this.editor.chain().focus().toggleOrderedList().run();
    },

    increaseIndent() {
      this.editor.chain().focus().sinkListItem("listItem").run();
    },

    decreaseIndent() {
      this.editor.chain().focus().liftListItem("listItem").run();
    },

    undo() {
      this.editor.chain().focus().undo().run();
    },

    redo() {
      this.editor.chain().focus().redo().run();
    },

    setLink() {
      const previousUrl = this.editor.getAttributes("link").href;
      const url = window.prompt("URL", previousUrl);

      // cancelled
      if (url === null) {
        return;
      }

      // empty
      if (!url) {
        this.editor.chain().focus().extendMarkRange("link").unsetLink().run();

        return;
      }

      // update link
      this.editor
        .chain()
        .focus()
        .extendMarkRange("link")
        .setLink({ href: url })
        .run();
    },

    setTextColor(color) {
      this.editor.chain().focus().setColor(color).run();
    },

    unsetTextColor() {
      this.editor.chain().focus().unsetColor().run();
    },

    setBgColor(color) {
      this.editor.chain().focus().setHighlight({ color }).run();
    },

    unsetBgColor() {
      this.editor.chain().focus().unsetHighlight().run();
    },

    handleFocusOut(label) {
      if (!label || this.labelError) {
        return;
      }
      this.openLabelInput = false;
    },

    handleLabelInput(evt) {
      let label = evt.target.value;
      this.labelError = "";
      let labelFound = false;
      this.panels.forEach((panel) => {
        if (!panel.fields.length) {
          return;
        }
        for (let field of panel.fields) {
          if (field.label.toLowerCase() === label.toLowerCase()) {
            labelFound = true;
            return;
          }
        }
      });
      if (labelFound) {
        this.labelError = "Specified label already assigned";
      }
      this.$emit("update:label", evt.target.value);
    },

    handleOpenInput() {
      if (this.labelEdit) {
        this.openLabelInput = true;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
#tip-tap-editor {
  margin: 16px;
}

.label1 {
  color: var(--icon-color);
  font-weight: 500;
  font-size: 13px;
  line-height: 1.25rem;
}

.label:hover {
  cursor: auto;
}
</style>
