import { extend } from "vee-validate";
import {
  // email,
  integer,
  min,
  max,
  min_value,
  max_value,
  alpha,
  alpha_spaces,
  alpha_num,
  alpha_dash,
} from "vee-validate/dist/rules";
import dayjs from "dayjs";
import { isArray, isEmpty } from "lodash-es";
import { validateFormula } from "./formula";

// required

extend("required", {
  validate(value) {
    if (typeof value === "number") {
      if (value > -1) {
        return {
          required: true,
          valid: true,
        };
      } else {
        return {
          required: true,
          valid: false,
        };
      }
    } else if (value === "<p></p>") {
      return {
        required: true,
        valid: false,
      };
    } else {
      return {
        required: true,
        valid: typeof value === "object" ? !isEmpty(value) : !!value,
      };
    }
  },
  computesRequired: true,
  message: "{_field_} should not be empty",
});

