// less than max

extend("lessThanMax", {
  params: ["target"],
  validate(value, { target }) {
    return target && value <= target;
  },
  message: "minimum should be less than maximum",
});

// ...

// less than max date

extend("lessThanMaxDate", {
  params: ["target"],
  validate(value, { target }) {
    return target && dayjs(value).isBefore(dayjs(target));
  },
  message: "minimum should be less than maximum",
});

// ...

// less than max time

extend("lessThanMaxTime", {
  params: ["target"],
  validate(value, { target }) {
    return (
      target &&
      dayjs(`2022-01-01 ${value}`).isBefore(dayjs(`2022-01-01 ${target}`))
    );
  },
  message: "minimum should be less than maximum",
});

// ...

// password

extend("password", (value) => {
  const passwordStrength =
    /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?!.*\s)(?=.*[#$^+=!*()@%&]).{8,16}$/g;

  if (passwordStrength.test(value)) {
    return true;
  } else if (value) {
    return false;
  }

  return "password is not valid";
});

