// min date

extend("minDate", {
  validate(value, { length }) {
    return dayjs(value).isSameOrAfter(dayjs(length));
  },
  params: ["length"],
  message: "should be greater than {length}",
});

// ...

// max date

extend("maxDate", {
  validate(value, { length }) {
    return dayjs(value).isSameOrBefore(dayjs(length));
  },
  params: ["length"],
  message: "should be lesser than {length}",
});

// ...

// min time

extend("minTime", {
  validate(value, { length }) {
    const _value = `2022-01-01 ${value}`;
    const _length = `2022-01-01 ${length}`;

    return dayjs(_value).isSameOrAfter(dayjs(_length));
  },
  params: ["length"],
  message: "should be greater than {length}",
});

// ...

// max time

extend("maxTime", {
  validate(value, { length }) {
    const _value = `2022-01-01 ${value}`;
    const _length = `2022-01-01 ${length}`;

    return dayjs(_value).isSameOrBefore(dayjs(_length));
  },
  params: ["length"],
  message: "should be lesser than {length}",
});

// ...

// fileTypes

extend("fileTypes", {
  validate(value, { types }) {
    const _types = isArray(types) ? types : [types];
    const files = Array.from(value);

    for (let file of files) {
      if (file.name) {
        const fileType = file.name.split(".").reverse()[0];

        if (!_types.includes(fileType)) {
          return `should be a ${_types.join(" | ")} file`;
        }
      }
    }

    return true;
  },
  params: ["types"],
});

// ...

// maxFileSize

extend("maxFileSize", {
  validate(value, { size }) {
    const files = Array.from(value);

    for (let file of files) {
      const fileSize = Math.floor(file.size / 1024 / 1024);

      if (fileSize > size) {
        return `each file size should not be more than ${size} mb`;
      }
    }

    return true;
  },
  params: ["size"],
});

// ...

// validFormula

extend("validFormula", (value) => {
  const isInvalidFormula = validateFormula(value);

  if (isInvalidFormula) {
    return "formula is not valid";
  }

  return true;
});

// ...

// checkDuplicate

extend("checkDuplicate", {
  params: ["target"],
  validate(value, { target }) {
    return target === "";
  },
  message: "Specified label already assigned",
});

// ...

// url

extend("url", (value) => {
  const urlValidation = new RegExp(
    "^(https?:\\/\\/)?" +
      "((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.?)+[a-z]{2,}|" +
      "((\\d{1,3}\\.){3}\\d{1,3}))" +
      "(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*" +
      "(\\?[;&a-z\\d%_.~+=-]*)?" +
      "(\\#[-a-z\\d_]*)?$",
    "i"
  );

  // console.log(urlValidation.test(value));

  if (urlValidation.test(value)) {
    return true;
  } else if (value) {
    return false;
  }

  return "url is not valid";
});

// ...

// url

extend("domain", (value) => {
  const domainValidation = new RegExp(
    /[a-zA-Z0-9][a-zA-Z0-9-]{1,61}[a-zA-Z0-9](?:\.[a-zA-Z]{2,})+/,
    "i"
  );

  if (domainValidation.test(value)) {
    return true;
  } else if (value) {
    return false;
  }

  return "domain is not valid";
});

// ...

// currency

extend("currency", (value) => {
  // let number = /^(\d*([.,](?=\d{3}))?\d+)+((?!\2)[.,]\d\d)?$/.test(value);
  let number = /^\d{1,3}(,?\d{3})*?(.\d{2})?$/g.test(value);

  // console.log(number);

  if (number) {
    return true;
  } else if (value) {
    return false;
  }

  return "value is not valid";
});

// ...

// decimal

extend("decimal", (value) => {
  let number = /^[1-9]\d*(\.\d+)?$/.test(value);

  // console.log(number);

  if (number) {
    return true;
  } else if (value) {
    return false;
  }

  return "value is not valid";
});

// ...

// commas

extend("commas", (value) => {
  let number = String(value).replace(new RegExp(",", "g"), "");
  // console.log(number);

  var isDecimal = /^\d+\.\d{0,2}$/.test(number);
  // console.log(isDecimal);

  if (!isDecimal) {
    if (Number(number)) {
      return true;
    } else {
      return false;
    }
  } else {
    return false;
  }
});

// ...

// commas
extend("both", (value) => {
  let number = String(value).replace(new RegExp(",", "g"), "");
  var isDecimal = /^\d+\.\d{0,2}$/.test(number);
  if (!isDecimal) {
    if (Number(number)) {
      return true;
    } else {
      return false;
    }
  } else {
    return true;
  }
});
