<template>
  <ValidationProvider
    v-slot="{ errors }"
    :name="field.label"
    :rules="validationRules"
    :class="{
      'is-disabled': isDisabled,
      [size]: size,
    }"
  >
    <NumberField
      v-if="contentRule === 'DECIMAL' || contentRule === 'INTEGER'"
      :value="value1"
      :label="field.settings.general.hideLabel ? '' : field.label"
      :is-mandatory="!_isReadonly ? isMandatory : false"
      :is-readonly="_isReadonly"
      :is-disabled="isDisabled"
      :placeholder="placeholder"
      :tooltip="tooltip"
      :error="errors[0]"
      :highlight="highlight"
      :is-search-field="isSearchField"
      :qr-value="qrValue"
      @input="handleInput"
      @verify="$emit('verify')"
      @enter="updateFormSearch(value1)"
      @updateFormSearch="updateFormSearch"
      @scanQRCode="scanQRCode"
    />

    <TextField
      v-if="contentRule === 'COMMAS' || contentRule === 'BOTH'"
      :value="value1"
      :label="field.settings.general.hideLabel ? '' : field.label"
      :is-mandatory="!_isReadonly ? isMandatory : false"
      :is-readonly="_isReadonly"
      :is-disabled="isDisabled"
      :placeholder="placeholder"
      :tooltip="tooltip"
      :error="errors[0]"
      :highlight="highlight"
      :is-search-field="isSearchField"
      :qr-value="qrValue"
      @input="handleInput"
      @verify="$emit('verify')"
      @focusOut="handleFocusOut"
      @enter="updateFormSearch(value1)"
      @updateFormSearch="updateFormSearch"
      @scanQRCode="scanQRCode"
    />
  </ValidationProvider>
</template>

<script>
import { ValidationProvider } from "vee-validate";
import NumberField from "@/components/common/form/number-field/NumberField.vue";
import TextField from "@/components/common/form/text-field/TextField.vue";
import { axiosCrypto } from "@/api/axios.js";

export default {
  name: "NumberFieldWrapper",

  components: { ValidationProvider, NumberField, TextField },

  props: {
    value: {
      type: [String, Number],
      required: true,
    },

    field: {
      type: Object,
      required: true,
    },

    isReadonly: {
      type: Boolean,
      default: false,
    },

    highlight: {
      type: String,
      default: "",
    },

    filter: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      value1: "",
    };
  },

  computed: {
    validationRules() {
      const rules = {};

      if (this.isMandatory && !this.isReadonly) {
        rules.required = true;
      }

      const contentRule = this.field.settings.validation.contentRule;
      if (contentRule === "INTEGER") {
        rules.integer = true;
      }

      if (contentRule === "COMMAS") {
        rules.commas = true;
      }

      if (contentRule === "BOTH") {
        rules.both = true;
      }

      const minimum = this.field.settings.validation.minimum;
      if (minimum) {
        rules.min_value = minimum;
      }

      const maximum = this.field.settings.validation.maximum;
      if (maximum) {
        rules.max_value = maximum;
      }

      return rules;
    },

    size() {
      return this.field.settings.general.size;
    },

    _isReadonly() {
      return (
        this.isReadonly ||
        this.field.settings.general.visibility === "READ_ONLY"
      );
    },

    isDisabled() {
      return this.field.settings.general.visibility === "DISABLE";
    },

    isMandatory() {
      return this.field.settings.validation.fieldRule === "REQUIRED";
    },

    placeholder() {
      return this.field.settings.general.placeholder;
    },

    tooltip() {
      return this.field.settings.general.tooltip;
    },

    contentRule() {
      return this.field.settings.validation.contentRule;
    },

    _value() {
      if (this.contentRule === "COMMAS") {
        let amount = this.value;
        let value = String(amount)
          .replace(new RegExp(",", "g"), "")
          .toString()
          .replace(/\B(?=(\d{3})+(?!\d))/g, ",");

        // console.log(value);

        return value;
      }

      return this.value;
    },

    isSearchField() {
      return this.field.settings.specific.isSearchField === "YES";
    },

    qrValue() {
      return this.field.settings.specific.qrValue;
    },

    // value1() {
    //   if (this.contentRule === "COMMAS") {
    //     var num = this.getNumber(this.value);
    //     console.log(num);
    //     if (num == 0) {
    //       return "";
    //     } else {
    //       return num.toLocaleString();
    //     }
    //   }
    //   return this.value;
    // },
  },

  watch: {
    filter: {
      // immediate: true,
      deep: true,
      handler() {
        if (this.field.filterBy) {
          if (this.field.settings.specific.masterFormId) {
            this.getMasterEntriesFilter(this.field);
          }
        }
      },
    },

    value: {
      immediate: true,
      handler() {
        this.value1 = this.value;
      },
    },
  },

  methods: {
    handleInput(value) {
      this.$emit("input", value);
    },

    getNumber(_str) {
      // console.log(_str);
      var arr = _str.split("");
      var out = new Array();
      for (var cnt = 0; cnt < arr.length; cnt++) {
        // console.log(isNaN(arr[cnt]));
        if (isNaN(arr[cnt]) == false) {
          out.push(arr[cnt]);
        }
      }
      return Number(out.join(""));
    },

    async getMasterEntriesFilter(field) {
      try {
        const response = await axiosCrypto.post(
          `/form/${field.settings.specific.masterFormId}/uniqueColumnValues`,
          JSON.stringify({
            column: field.settings.specific.masterFormColumn,
            keyword: "",
            rowFrom: 0,
            rowCount: 0, // + 10,
            filters: field.filterBy,
          })
        );
        const { status, data } = response;
        if (status !== 200) {
          throw response;
        }
        const options = JSON.parse(data);
        if (options.length) {
          if (options.length === 1) {
            this.handleInput(options[0]);
          }
          this.$emit("clearFilter");
        } else {
          this.handleInput("");
          this.$emit("clearFilter");
        }
      } catch (e) {
        console.error(e);
        this.$alert.error("Error fetching options");
      }
    },

    numberValidation() {
      if (this.contentRule === "COMMAS" || this.contentRule === "BOTH") {
        // let amount = this.value;
        // let value = String(amount)
        //   .replace(new RegExp(",", "g"), "")
        //   .toString()
        //   .replace(/\B(?=(\d{3})+(?!\d))/g, ",");

        // // console.log(value);

        // return value;
        let amount = this.value.split(".");
        let value = amount[0]
          .replace(new RegExp(",", "g"), "")
          .toString()
          .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        // if()
        if (amount[1]) {
          let value1 = value.replace(new RegExp(",", "g"), "");
          let format = `${Number(value1)}.${amount[1]}`;
          // console.log(format);
          let price = Number(format).toFixed(2);
          let price1 = price
            .replace(new RegExp(",", "g"), "")
            .toString()
            .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
          return price1;
        }
        return value;
      }

      return this.value;
    },

    handleFocusOut() {
      this.value1 = this.numberValidation();
    },

    updateFormSearch(value) {
      this.$emit("updateFormSearch", value, this.field);
    },

    scanQRCode() {
      this.$emit("scanQRCode", this.field);
    },
  },
};
</script>

<style lang="scss" scoped></style>
