<template>
  <ValidationProvider v-slot="{ errors }" :name="field.label" class="col-12">
    <FillInTheBlanksField
      :value="value"
      :label="field.settings.general.hideLabel ? '' : field.label"
      :is-readonly="_isReadonly"
      :is-disabled="isDisabled"
      :tooltip="tooltip"
      :error="errors[0]"
      :content="content"
      :fib-fields="fibFields"
      @input="handleInput"
    />
  </ValidationProvider>
</template>

<script>
import { ValidationProvider } from "vee-validate";
import FillInTheBlanksField from "@/components/common/form/fill-in-the-blanks-field/components/RenderContent.vue";

export default {
  name: "FillInTheBlanksFieldWrapper",

  components: { ValidationProvider, FillInTheBlanksField },

  props: {
    value: {
      type: Object,
      required: true,
    },

    field: {
      type: Object,
      required: true,
    },

    isReadonly: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    _isReadonly() {
      return (
        this.isReadonly ||
        this.field.settings.general.visibility === "READ_ONLY"
      );
    },

    isDisabled() {
      return this.field.settings.general.visibility === "DISABLE";
    },

    tooltip() {
      return this.field.settings.general.tooltip;
    },

    content() {
      return this.field.settings.specific.textContent;
    },

    fibFields() {
      return this.field.settings.specific.fibFields;
    },
  },

  methods: {
    handleInput(value) {
      this.$emit("input", value);
    },
  },
};
</script>

<style lang="scss" scoped></style>
