<template>
  <div id="app">
    <h1>Firebase Data</h1>
    <form @submit.prevent="addData">
      <input v-model="newData" placeholder="Enter some data" />
      <button type="submit">Submit</button>
    </form>
    <ul>
      <li v-for="(item, index) in data" :key="index">{{ item.value }}</li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      newData: "",
      data: [],
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      axios
        .get("http://127.0.0.1:5001/data")
        .then((response) => {
          this.data = Object.values(response.data || {});
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    },
    addData() {
      axios
        .post("http://127.0.0.1:5001/data", { value: this.newData })
        .then(() => {
          this.fetchData();
          this.newData = "";
        })
        .catch((error) => {
          console.error("Error adding data:", error);
        });
    },
  },
};
</script>

<style>
/* Add any styles you want here */
#app {
  text-align: center;
}
form {
  margin-bottom: 20px;
}
</style>
