<template>
  <ValidationProvider v-slot="{ errors }" :rules="validationRules">
    <TextField
      :value="value"
      :is-readonly="_isReadonly"
      :is-disabled="isDisabled"
      :placeholder="placeholder"
      :is-clearable="false"
      :error="errors[0]"
      @input="handleInput"
    />
  </ValidationProvider>
</template>

<script>
import { ValidationProvider } from "vee-validate";
import TextField from "@/components/common/form/text-field/TextField.vue";
import { axiosCrypto } from "@/api/axios.js";

export default {
  name: "ShortTextFieldWrapper",

  components: { ValidationProvider, TextField },

  props: {
    value: {
      type: String,
      required: true,
      default: "",
    },

    field: {
      type: Object,
      required: true,
    },

    isReadonly: {
      type: Boolean,
      default: false,
    },

    filter: {
      type: Boolean,
      default: false,
    },

    filterBy: {
      type: Array,
      default: () => [],
    },

    isMandatory: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      options: [],
    };
  },

  computed: {
    validationRules() {
      const rules = {};

      if (this._isMandatory && !this.isReadonly) {
        rules.required = true;
      }

      if (this.contentRule === "ALPHA") {
        rules.alpha = true;
      }

      if (this.contentRule === "ALPHA_SPACES") {
        rules.alphaSpaces = true;
      }

      if (this.contentRule === "ALPHA_DASH") {
        rules.alphaDash = true;
      }

      if (this.contentRule === "ALPHA_NUMERIC") {
        rules.alphaNum = true;
      }

      if (this.contentRule === "EMAIL") {
        rules.email = true;
      }

      const minCharacters = this.field.settings.validation.minimum;
      if (minCharacters) {
        rules.min = minCharacters;
      }

      const maxCharacters = this.field.settings.validation.maximum;
      if (maxCharacters) {
        rules.max = maxCharacters;
      }

      return rules;
    },

    _isReadonly() {
      return (
        this.isReadonly ||
        this.field.settings.general.visibility === "READ_ONLY"
      );
    },

    isDisabled() {
      return this.field.settings.general.visibility === "DISABLE";
    },

    _isMandatory() {
      if (this.isMandatory) {
        return true;
      } else {
        return this.field.settings.validation.fieldRule === "REQUIRED";
      }
    },

    contentRule() {
      return this.field.settings.validation.contentRule;
    },

    placeholder() {
      return this.field.settings.general.placeholder;
    },
  },

  watch: {
    filter: {
      immediate: true,
      deep: true,
      handler() {
        if (this.filterBy) {
          if (this.filterBy.length) {
            if (this.field.settings.specific.masterFormId) {
              this.getMasterEntriesFilter();
            }
          }
        }
      },
    },
  },

  created() {},

  methods: {
    handleInput(value) {
      this.$emit("input", value);
    },

    async getMasterEntriesFilter() {
      let filterBy = [];
      this.filterBy.forEach((item) => {
        filterBy.push({ ...item, value: item.value.toString() });
      });
      try {
        const response = await axiosCrypto.post(
          `/form/${this.field.settings.specific.masterFormId}/uniqueColumnValues`,
          JSON.stringify({
            column: this.field.settings.specific.masterFormColumn,
            keyword: "",
            rowFrom: 0,
            rowCount: 0, // + 10,
            filters: filterBy,
          })
        );
        const { status, data } = response;
        if (status !== 200) {
          throw response;
        }
        const options = JSON.parse(data);
        if (options.length) {
          this.options = options.map((option) => ({
            id: this.$nano.id(),
            label: option,
            value: option,
          }));

          this.handleInput(this.options[0].value);
        } else {
          this.handleInput("");
        }
      } catch (e) {
        console.error(e);
        this.$alert.error("Error fetching options");
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
